.main-navigation {
	display: block;
	width: 100%;

	ul {
		display: none;
		list-style: none;
		margin: 0;
		padding-left: 0;

		// ul {
		// 	// box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
		// 	position: absolute;
		// 	top: 100%;
		// 	left: -999em;
		// 	z-index: 99999;

		// 	// ul.sub-menu {
		// 	// 	left: -999em;
		// 	// 	top: 0;
		// 	// }
		// }
		li#menu-item-258,
		li#menu-item-309 {
			padding: 0;
			a {
				padding: 1rem;
				margin: 0;
			}
			ul.sub-menu {
				display: none;
				margin: 0;
				padding: 0;
				background-color: #f5f5f5;
				a {
					padding: 0;
					// margin-left: 1rem;
				}
			}
		}
	}

	li {
		position: relative;
	}

	a {
		display: block;
		text-decoration: none;
	}
}

.site-header {
	position: fixed !important;
	background-color: white;
	z-index: 9999;
	top: 0;
	left: 0;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0.5rem 1.5rem;
	.site-title {
		display: none;
	}

	.site-branding {
		img {
			width: 200px;
		}
	}

	position: relative;

	.menu-main-menu-container {
		display: none;
		position: absolute;
		top: 100%;
		left: 0;
		width: 100%;

		ul {
			display: flex;
			flex-direction: column;
			// gap: 1rem;
			list-style-type: none;
			margin: 0;

			li {
				padding: 1rem;
			}
		}
	}

	.main-navigation.toggled {
		.menu-main-menu-container {
			display: block;
			height: 100vh;
		}
	}
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: flex;
	border: none;
	background-color: white;
	text-transform: uppercase;
	letter-spacing: 1px;
	white-space: nowrap;
}

#site-navigation.main-navigation {
	width: 40px;
}

nav li#menu-item-157 {
	background-color: $bcls__orange;
}

@media screen and (min-width: 48em) {
	.menu-toggle {
		display: none;
	}
	nav li#menu-item-157 {
		background-color: $bcls__orange;
		width: fit-content;
		border-radius: 0.35rem;
		height: 40px;
		display: flex;
		align-items: center;
		color: #fff;
		padding: 0.5rem 1rem;
		font-weight: 500;
		&:hover {
			background-color: darken($bcls__orange, 5%);
		}
		a,
		a:visited,
		a:hover {
			color: #fff;
		}
	}

	.site-header {
		display: flex;
		align-items: center;
		padding: 1.5rem 2.8rem;
		font-size: 0.9rem;
		gap: 0.5rem;
		justify-content: space-between;

		.site-branding {
			img {
				min-width: 200px;
				max-width: 300px;
			}
		}

		.main-navigation ul {
			display: flex;
			gap: 1rem;
			text-transform: uppercase;
			letter-spacing: 1px;
			white-space: nowrap;
			align-items: center;

			li#menu-item-258,
			li#menu-item-309 {
				ul.sub-menu {
					display: none;
					position: absolute;
					top: 100%;
					left: auto;
					z-index: 99999;
					width: fit-content;

					li {
						background-color: white;

						&:hover {
							background-color: #f5f5f5;
						}
					}
				}

				&:hover ul {
					display: flex;
					flex-direction: column;
					background-color: white;
					gap: 0;
					box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);

					li {
						border-bottom: #ccc solid 1px;
						width: 100%;
						// a {
						// 	padding: 1rem;
						// 	margin: 0;
						// }
					}
				}
			}
		}

		.menu-main-menu-container {
			display: flex;
			position: relative;
			ul {
				position: absolute;
				top: -28px;
				right: 0;
				display: flex;
				flex-direction: row;
			}
		}
	}
}

@media screen and (min-width: 42em) {
	.main-navigation ul {
		gap: 1.4rem;
	}
}

button.menu-toggle {
	margin: 0;
	padding: 0;
}
#nav-icon {
	width: 35px;
	height: 20px;
	position: relative;
	margin: 20px auto;
	-webkit-transform: rotate(0deg);
	-moz-transform: rotate(0deg);
	-o-transform: rotate(0deg);
	transform: rotate(0deg);
	-webkit-transition: 0.5s ease-in-out;
	-moz-transition: 0.5s ease-in-out;
	-o-transition: 0.5s ease-in-out;
	transition: 0.5s ease-in-out;
	cursor: pointer;
}

#nav-icon span {
	display: block;
	position: absolute;
	height: 3px;
	width: 100%;
	background: black;
	border-radius: 2px;
	opacity: 1;
	left: 0;
	-webkit-transform: rotate(0deg);
	-moz-transform: rotate(0deg);
	-o-transform: rotate(0deg);
	transform: rotate(0deg);
	-webkit-transition: 0.25s ease-in-out;
	-moz-transition: 0.25s ease-in-out;
	-o-transition: 0.25s ease-in-out;
	transition: 0.25s ease-in-out;
}

#nav-icon span:nth-child(1) {
	top: -3px;
	-webkit-transform-origin: left center;
	-moz-transform-origin: left center;
	-o-transform-origin: left center;
	transform-origin: left center;
}

#nav-icon span:nth-child(2) {
	top: 9px;
	-webkit-transform-origin: left center;
	-moz-transform-origin: left center;
	-o-transform-origin: left center;
	transform-origin: left center;
}

#nav-icon span:nth-child(3) {
	top: 22px;
	-webkit-transform-origin: left center;
	-moz-transform-origin: left center;
	-o-transform-origin: left center;
	transform-origin: left center;
}

nav.toggled #nav-icon span:nth-child(1) {
	-webkit-transform: rotate(45deg);
	-moz-transform: rotate(45deg);
	-o-transform: rotate(45deg);
	transform: rotate(45deg);
	top: -3px;
	left: 8px;
}

nav.toggled #nav-icon span:nth-child(2) {
	width: 0%;
	opacity: 0;
}

nav.toggled #nav-icon span:nth-child(3) {
	-webkit-transform: rotate(-45deg);
	-moz-transform: rotate(-45deg);
	-o-transform: rotate(-45deg);
	transform: rotate(-45deg);
	top: 21px;
	left: 8px;
}
