.section-register {
	background-image: url(https://raw.githubusercontent.com/biaoliveira09/bclandsummit/1565f8f23b7ef877043e3ec4cc06d9089f3abd60/assets/nanaimo.webp);
	background-size: cover;
	background-size: auto;
	background-repeat: no-repeat;
	background-position: center;
	background-attachment: fixed;
	height: 100vh;
	position: relative;
	color: white;
	margin-top: 4rem;

	&::after {
		content: '';
		background-image: linear-gradient(
			to bottom,
			rgba(56, 32, 128, 0.841),
			rgba(79, 56, 146, 0.85)
		);
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 1;
	}
	.reg-container {
		position: relative;
		z-index: 3;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		padding: 2rem 0;
		height: 100%;
	}

	h2 {
		position: relative;
		display: inline-block;
		z-index: 3;
		font-size: 2rem;
		text-align: center;
	}

	h3 {
		font-family: 'Poppins', sans-serif;
		font-size: 1.3rem;
		font-weight: 400;
		line-height: 1.5;
		letter-spacing: 2px;
		text-transform: uppercase;
		position: relative;
		z-index: 3;
		text-align: center;
	}

	img {
		height: 200px;
	}
}

@media screen and (min-width: 43em) {
	.section-register {
		margin-top: 5rem;
		h2 {
			padding-top: 5rem;
		}
		img {
			height: 350px;
		}
		h3 {
			padding-bottom: 8rem;
		}
	}
}

@media screen and (min-width: 43em) {
	.section-register {
		.reg-container {
			padding: 9rem 0;
		}
	}
}
