/* Navigation
--------------------------------------------- */
.main-navigation {
	display: block;
	width: 100%;
}
.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
	padding-left: 0;
}
.main-navigation ul li#menu-item-258,
.main-navigation ul li#menu-item-309 {
	padding: 0;
}
.main-navigation ul li#menu-item-258 a,
.main-navigation ul li#menu-item-309 a {
	padding: 1rem;
	margin: 0;
}
.main-navigation ul li#menu-item-258 ul.sub-menu,
.main-navigation ul li#menu-item-309 ul.sub-menu {
	display: none;
	margin: 0;
	padding: 0;
	background-color: #f5f5f5;
}
.main-navigation ul li#menu-item-258 ul.sub-menu a,
.main-navigation ul li#menu-item-309 ul.sub-menu a {
	padding: 0;
}
.main-navigation li {
	position: relative;
}
.main-navigation a {
	display: block;
	text-decoration: none;
}

.site-header {
	position: fixed !important;
	background-color: white;
	z-index: 9999;
	top: 0;
	left: 0;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0.5rem 1.5rem;
	position: relative;
}
.site-header .site-title {
	display: none;
}
.site-header .site-branding img {
	width: 200px;
}
.site-header .menu-main-menu-container {
	display: none;
	position: absolute;
	top: 100%;
	left: 0;
	width: 100%;
}
.site-header .menu-main-menu-container ul {
	display: flex;
	flex-direction: column;
	list-style-type: none;
	margin: 0;
}
.site-header .menu-main-menu-container ul li {
